    <h3>考研倒计时</h3>
    <div>1天</div>
    <div>1天</div>
    <div>1天</div>
    <div>1天</div>
  <van-button type="info" @click="changeText" class="but_der" size="small">换一条<p>111</p></van-button>
    <div class="text_der">
      <i>{{text}}</i>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'MyHome',
  data(){
    return{
      text:''
    }
  },
  mounted() {
    this.getFamous()
  },
  methods: {
    async getFamous() {
      let {status,data} = await this.$http.get('/api')
      if(status==200){
        this.text = data
      }
    },
    changeText(){
      this.getFamous()
    }
  }
}
</script>

